import React, {useState} from 'react'

export default function TextForm(props) {
    const handleUpClick = () => {
        console.log("Upper case button was clicked" + text);
        let newText = text.toUpperCase();
        setText(newText)
        props.showAlert("converted to Uppercase", "success")
    }

    const handleLoClick = () => {
        console.log("Lower case button was clicked" + text);
        let newText = text.toLowerCase();
        setText(newText)
        props.showAlert("converted to Lowercase", "success")
    }

    const handleClearText = () => {
        console.log("Clear Text button was clicked");
        let newText = "";
        setText(newText)
        props.showAlert("text  cleared", "success")
    }

    const handleOnChange = (event) => {
        console.log("onChange");
        setText(event.target.value);
    }

    const [text, setText] = useState('Enter the text here');

    return (
        <>
            <div className="container"
                style={
                    {
                        color: props.mode === "dark" ? "white" : "#042743"
                    }
            }>
                <h1>{
                    props.heading
                }</h1>
                <div className="mb-3">
                    <textarea className="form-control"
                        onChange={handleOnChange}
                        style={
                            {
                                backgroundColor: props.mode === "dark" ? "grey" : "white",
                                color: props.mode === "dark" ? "white" : "#042743"
                            }
                        }
                        value={text}
                        id="myBox"
                        rows="8"></textarea>
                </div>
                <button className="btn btn-primary mx-2"
                    onClick={handleUpClick}>Convert to Uppercase</button>
                <button className="btn btn-primary mx-2"
                    onClick={handleLoClick}>Convert to Uppercase</button>
                <button className="btn btn-primary mx-2"
                    onClick={handleClearText}>Clear Text</button>
            </div>
            <div className="container my-3"
                style={
                    {
                        color: props.mode === "dark" ? "white" : "#042743"
                    }
            }>
                <h1>
                    Your Text summary here
                </h1>
                <p> {
                    text.split(" ").length
                }
                    words and {
                    text.length
                }
                    characters
                </p>
                <p>Estimated time to read is : {
                    0.008 * text.split(" ").length
                }
                    minutes
                </p>
                <h2>
                    Preview</h2>
                <p>{
                    text.length > 0 ? text : "Enter something in the textbox to preview"
                }</p>
            </div>
        </>
    )
}
